*{
  box-sizing: border-box;
}
.body{
  margin: 0px;
  background: #efefef;
  font-family: 'Permanent Marker', cursive;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center; 
  height: 100vh;
  background: rgb(241, 248, 248);
}
.outer_box{
  width: 600px;
  border-radius: 12px;
  background: rgb(233, 245, 245);
  box-shadow: 0 0 20px rgb(230, 228, 228);

}
.game_box{
  display: grid;
  grid-template-columns: repeat(3,7.5rem);
  justify-content: center;
  place-items: center;
}
.player_info{
  color: rgb(34, 34, 36);
  text-align: center;
  font-size: 32px;
}
.winner_h1{
  color: rgb(68, 68, 145);
  text-align: center;
  font-size: 40px;

}
.reset-button{
  border: none;
  border-radius: 0.5rem;
  background: rgb(68, 68, 145);
  color: white;
  font-size: 2rem;
  padding: 0.5rem 1rem;
  margin: 2rem auto;
  display: block;
  /* font-family: 'Permanent Marker', cursive; */
}

.x{
  color: rgb(114, 11, 11);
}
.o{
  color: rgb(14, 14, 107);
}
.info_div{
  margin-top: 190px;

}





.play_button{
  border: 1px solid blue;
  background: rgb(166, 166, 213);
  border-radius: 12px;
  padding: 10px 10px;
  color: #ac2a2a;
  font-size: 18px;
}
.popup_div{
  
  width: 600px;
  height: 200px;
  background: rgb(224, 205, 242);
  position: absolute;
  left: 200px;
  z-index: 1;
  box-shadow: 2px 2px 5px rgb(93, 93, 133);
  border-radius: 12px;
}
.popup_div p{
  text-align: center;
  font-size: 25px;
  margin-top: 30px;
}
.popup_div button{
  width: 200px;
  border: 1px solid blue;
  background: rgb(166, 166, 213);
  border-radius: 12px;
  color: #ac2a2a;
  font-size: 18px;
  margin: 20px 200px;
  padding: 10px 0;
  font-weight: bold;
}

.d_none{
  display: none;
}


@media screen and (max-width:600px) {
  
  .game_box{
    grid-template-columns: repeat(3,5.5rem);
  }
  .outer_box{
    width: 400px;
  }
  .winner_h1{
    font-size: 30px;
  }


  .popup_div{
  display: flex;
  justify-content: center;
  flex-direction: column;
    width: 300px;
    height: 300px;
    position: absolute;
    left: 10px;
    top: 150px;
    z-index: 1;
  }
.popup_div button{
  margin: 20px 50px;
  font-size: 20px;

}
  
}






 
