.box{
    width: 7rem;
    height: 7rem;
    cursor: pointer;
    margin:0.2rem;
    font-size: 5em;
    font-weight: bold;
    background: white;
    border: none;
    border-radius: 10%;
    box-shadow: 0 0 8px #888;
}
.box:hover{
    box-shadow: 0 0 13px #888;

}
.x{
    color: rgb(114, 11, 11);
}
.o{
    color: rgb(14, 14, 107);
}

@media screen and (max-width: 600px) {
    .box{
        width: 80px;
        height: 80px;
    }
    
}
@media screen and (max-width: 400px) {
    .box{
        width: 80px;
        height: 70px;
    }
    
}